<template lang="html">
  <div id="headLine">
    <div class="hlImg">
      <img :src="headLineImg" alt="" />
    </div>
    <div class="latest-icon">{{ latestTag }}</div>
    <div class="latest-text">
      <div>{{ latestText }}</div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        headLineImg: "../../../static/images/tbtt.png",
        countLatestNews: 0,
        latestTextArr: [
          { text: "+" ,tag: ""},
          { text: "" ,tag: ""},
          { text: "" ,tag: ""},
          { text: "" ,tag: ""},
          { text: "" ,tag: ""}
        ],
        latestText: "",
        latestTag: ""
      }
    },
    mounted () {
      var len = this.latestTextArr.length-1;
      this.latestText = this.latestTextArr[0].text;
      this.latestTag = this.latestTextArr[0].tag;
      setInterval(()=>{
        if(this.countLatestNews>=len) this.countLatestNews = 0;
        else this.countLatestNews++;
        this.latestText = this.latestTextArr[this.countLatestNews].text;
        this.latestTag = this.latestTextArr[this.countLatestNews].tag;
      },3000)
    }
  }
</script>

<style lang="scss">
  #headLine{
    height: .3rem;
    width: 100%;
    margin: .1rem 0 .05rem;
    display: flex;
    align-items: center;
    border-bottom: .06rem solid #eee;
    .hlImg{
      width: .8rem;
      padding-right: .06rem;
      text-align: center;
    }
    img,.hlImg{
      height: .21rem;
    }
    .latest-icon{
      line-height: .18rem;
      font-size: .16rem;
      color: rgb(216, 20, 33);
      border-radius: 4px;
      border: 1px solid rgb(216, 20, 33)
    }
    .latest-text{
      flex: 1;
      white-space: nowrap;
      overflow: hidden;
      padding: 0 .06rem;
      font-size: .16rem;
      color: #444;

      div{
        overflow: hidden;
      }
    }
  }
</style>



